.compare-container {
    height: 100%;
    width: 100%;
    .empty {
        display: flex;
        justify-content: center;
        align-items: center;
        gap: 3rem;
        flex-direction: column;
        height: 100%;
        button {
            cursor: pointer;
            border-radius: 10rem;
            padding: 2rem;
            display: flex;
            justify-content: center;
            align-items: center;
            border: non;
            background-color: var(--accent-color);
            svg {
                font-size: 5rem;
                color: whitesmoke;
            }
        }
        h3 {
            color: white;
            text-transform: uppercase;
            letter-spacing: 0.3rem;
        }
    }
}

.compare-element {
    height: 100%;
    display: grid;
    grid-template-rows: 90% 10%;
    grid-template-columns: 1fr;

    .compare-action-buttons {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        width: 100%;
        height: 100%;
        button {
            font-size: 1.2rem;
            font-weight: 400;
            text-transform: uppercase;
            
            color: rgb(228, 228, 228);
            cursor: pointer;
            background-color: transparent;
            letter-spacing: 0.1rem;
            border: 1px solid rgba(255, 255, 255, 0.047);
            transition: 0.3s ease-in-out;
            &:nth-of-type(1){
                &:hover {
                    background-color: #1f51ff;
                    border-color: #1f51ff;

                }
            }
            &:nth-of-type(2){
                &:hover {
                    background-color: #27af0f;
                    border-color: #27af0f;

                }
            }
            &:nth-of-type(3){
                &:hover {
                    background-color: #e21b5a;
                    border-color: #e21b5a;

                }
            }
        }
    }

    .compare-info {
        color: white;
        text-transform: uppercase;
        letter-spacing: 0.1rem;
        width: 100%;
        display: grid;
        grid-template-rows: 40% 60%;

        
        .strategy-types-container {
            width: 100%;
            display: flex;
            flex-direction: column;
            gap: 1rem;
            align-items: flex-start;
            max-height: 100%;
            overflow-y: scroll;
            overflow-x: hidden;
            padding-bottom: 1rem;

            .strategy-types {
                display: grid;
                grid-template-columns: 25% 75%;
                gap: 2rem;
                width: 100%;
                .strategy-type-title {
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                }
                .strategy-type-icons {
                    display: flex;
                    gap: 1rem;
                    width: 100%;
                    flex-wrap: wrap;

                    .strategy-type {
                        display: flex;
                        justify-content: flex-end;
                        align-items: center;
                        .strategy-type-image{
                            height: 3rem;
                            width: 3rem;
                        }
                    }
                }
            }
        }
    }
    .compare-details {
        display: flex;
        flex-direction: collumn;
        justify-content: center;
        align-items: center;

        .compare-image {
            height: 10rem;
        }
    }

}